html, body {
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

:root {
  --primary-color: #00acdc;
}

header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.navigation-list {
  align-items: stretch;
  height: 60px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.navigation-item {
  margin-left: 20px;
}

.nav-link {
  color: #000;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 100%;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-link:hover, .is-active {
  color: var(--primary-color);
}

.navigation-list .navigation-item:first-child {
  margin-left: 0;
}

.navigation-list .navigation-item:last-child {
  margin-right: 0;
}

.is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
